* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

a,
a:hover {
  text-decoration: none;
  display: inline-block; }

input,
button,
input:focus,
button:focus {
  outline: none;
  border: none; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

.header {
  width: 100%;
  height: 89px; }
  .header-nav {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000000;
    height: 89px;
    position: fixed;
    z-index: 999; }
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header-menu {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .header-menu__items_link {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 200;
      font-size: 15px;
      color: #FFFFFF; }
      .header-menu__items_link.active {
        font-family: 'Montserrat', sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 15px;
        color: #FFFFFF; }
      .header-menu__items_link:hover {
        font-weight: 700; }
    .header-menu__logo {
      display: flex;
      justify-content: center;
      align-items: center; }
      .header-menu__logo_items-link {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 30px;
        color: #FFFFFF;
        text-transform: uppercase; }
    .header-menu__search {
      display: flex;
      justify-content: center;
      align-items: center; }
      .header-menu__search_items {
        margin-left: 25px;
        position: relative; }
        .header-menu__search_items:nth-child(3)::before {
          content: '';
          position: absolute;
          left: 12px;
          top: 2px;
          width: 5px;
          height: 5px;
          background: #EB2A2A;
          border-radius: 50%; }
        .header-menu__search_items-link {
          font-family: Montserrat;
          font-style: normal;
          font-weight: normal;
          font-size: 15px;
          line-height: 29px;
          color: #FFFFFF; }
          .header-menu__search_items-link .fa {
            margin-right: 5px; }
          .header-menu__search_items-link img {
            margin-right: 5px;
            transform: rotate(180deg); }

.slider {
  width: 100%;
  height: 100%;
  background: #E5E5E5;
  padding-bottom: 50px; }
  @media (max-width: 1199.98px) {
    .slider {
      text-align: center; } }
  .slider .slider-container {
    background: #fff; }
  .slider-1 {
    display: flex;
    flex-direction: column;
    position: relative; }
    .slider-1__ellipse {
      width: 120px;
      height: 120px;
      margin: 50px auto; }
    .slider-1__text {
      font-family: Montserrat;
      font-style: normal;
      font-weight: bold;
      font-size: 40px;
      line-height: 35px;
      color: #000000;
      margin: 0 auto; }
      @media (max-width: 1199.98px) {
        .slider-1__text {
          margin-bottom: 50px; } }
    .slider-1__img_img {
      position: absolute;
      width: 100%;
      left: 50%;
      bottom: -20%;
      z-index: 999;
      transform: translateX(58.5%) scale(2.5);
      cursor: pointer; }
      @media (max-width: 1199.98px) {
        .slider-1__img_img {
          transform: scale(1);
          width: 100%;
          display: none; } }
  .slider-2 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative; }
    .slider-2__img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .slider-2__btn {
      padding: 5px 25px;
      background: #000000;
      font-size: 15px;
      border-radius: 50px;
      color: #fff;
      font-family: 'Montserrat';
      position: absolute;
      bottom: 25px; }
  .slider-3 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    .slider-3__title {
      margin-top: 150px;
      font-family: Montserrat;
      font-style: normal;
      font-weight: bold;
      font-size: 25px;
      line-height: 44px;
      color: #000000; }
      .slider-3__title span {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 40px;
        color: #000000; }
    .slider-3__subtitle {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: bold;
      font-size: 40px;
      color: #000000;
      margin-top: 50px; }
      @media (max-width: 1199.98px) {
        .slider-3__subtitle {
          margin-bottom: 50px; } }

